a,
strong,
input,
button,
textarea,
select,
dl,
dd,
dt {
    margin: 0;
    padding: 0;
}


body {
    font-family: "microsoft yahei", "Arial";
    font-size: .24rem;
    line-height: 1;
    -webkit-user-select: none;
    -webkit-text-size-adjust: 100% !important;
