    -webkit-box-sizing: border-box;
    box-sizing: border-box;
}


li {
    list-style-type: none;
    float: left;
}

a {
    text-decoration: none;
    outline: none;
}

img {
    border: none;
}

i,
var,
em {
    font-style: normal;
}

button {
    font-family: "microsoft yahei", "Arial";
    outline: none;
    cursor: pointer;
    font-size: .24rem;
    border: none;
}

iframe {
    border: none;
}

input,
select,
textarea {
    outline: none;
    font-family: "microsoft yahei", "Arial";
    font-size: .24rem;
    border-radius: 0;
    border: none;
    background: none;
